<template>
  <vs-row vs-justify="center" vs-align="flex-end" class="full-height login-register-bg">
    <vs-col type="flex"  vs-justify="center" class="login-register-box" vs-lg="12" vs-xs="12">

    <vs-card class="mb-0 bg-vscard-login">

      <vs-alert v-if="errors.length" class="mb-3 mt-2"  color="danger" :active="inputValid">
        <ul class="common-list text-center">
          <li v-for="error in errors" :key="error">{{ error }}</li>
        </ul>
      </vs-alert>
      
      <form @submit="checkForm" novalidate="true">
        <vs-input icon-after="true" size="large" class="w-100 mb-1 mt-2 vs-input-large" icon="person" placeholder="Email" v-model="email" type="email"/>
        <vs-input icon-after="true" size="large" class="w-100 mb-3 vs-input-large" type="password" icon="lock" placeholder="Password" v-model="pwd"/>
        <input class="btn-block btn btn-primary submit-button" type="submit" @click.stop.prevent="checkForm" value="Login"/>
      </form>

      <!-- <div class="d-flex justify-content-center mt-3 text-light">
       Create a new account?  &nbsp;<router-link to="/register"> Register</router-link>
     </div> -->

    </vs-card>
    </vs-col>
  </vs-row>   
  
</template>

<script>
export default {
  name: 'Login',
  data:()=>({
    msg: "เข้าสู่ระบบ",
    rememberme:false,
    email:'',
    pwd:'',
    errors:[],
  }),
  methods:{
    checkForm:function(e) {
      this.errors = [];
      if(!this.email) {
        this.errors.push("ป้อนอีเมล์ก่อน");
      } else if(!this.validEmail(this.email)) {
        this.errors.push("รูปแบบอีเมล์ไม่ถูกต้อง");        
      }else {
        this.$router.push('/dashboard');
      }
      if(!this.pwd) this.errors.push("ป้อนรหัสผ่านก่อน");
      if(!this.errors.length) return true;
      e.preventDefault();
    },
    /* eslint-disable */
    validEmail:function(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
    return re.test(email);
    }
    /* eslint-enable */
  },  
  computed: {
    inputValid(){
      if(this.validEmail(this.email) && this.pwd){
        return false
      } else {
        return true
      }
    }
  }
}
</script>

<style>

.login-register-bg{
  margin:0 auto;
  background:url(../../assets/images/background/auth-bg.jpg);
  position: relative;
  background-repeat: no-repeat;
  background-position-x: left;

}
.login-register-box{
  max-width:350px;
  margin-bottom: 60px;
}

.vs-card--header{
  margin-bottom: 0px;
}

.bg-vscard-login{
  background: transparent;
  box-shadow: none;
}

.con-vs-alert-danger{
  color: #EE1C27;
  background-color: #fdd835;
}

</style>

